import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { ServiceCategoryComponent } from './service-category.component';
import { NgZorroAntModule } from '../../../../ng-zorro-ant.module';
import { DetailCategoryServicesComponent } from './detail-category-services/detail-category-services.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { ScrollingModule } from '@angular/cdk/scrolling';
const routes: Routes = [
  {
    path: '',
    component: ServiceCategoryComponent
  },
  {
    path: 'detail',
    component: DetailCategoryServicesComponent
  }
]

@NgModule({
  declarations: [
    ServiceCategoryComponent,
    DetailCategoryServicesComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    ScrollingModule
  ]
})
export class ServiceCategoryModule { }
